/* chatbot Box */
.format {

    list-style-type:none;
    display:flex;
    flex-direction: column-reverse;
    overflow-y:auto;
    height:600px;
    border-top:2px solid #fff;
    border-bottom:2px solid #fff;
}

.chat_wrap {
    position: fixed;
    bottom: 20px;
    /* right: 20px; */
    right: -500px;
    width: 400px;
    border-radius: 8px;
    background-color: #fff;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    z-index: 999;
    font-family: Arial, Helvetica, sans-serif;
    transition: right 0.5s ease-in-out;
  }

  .chat_wrap.active {
    right: 20px; /* 버튼을 누르면 chat_wrap가 우측에서 출력되도록 변경된 right 값 설정 */
  }
  
  .chat_wrap .header {
    background-color: #1c7731;
    color: #fff;
    padding: 12px;
    font-size: 16px;
    font-weight: bold;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
  }
  
  .chat_wrap .chat {
    overflow-y: scroll;
    max-height: 500px;
  }
  
  .chat_wrap .chat ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .chat_wrap .chat li {
    margin: 10px;
    padding: 10px;
    border-radius: 8px;
    background-color: #f2f2f2;
  }

  .chat_wrap .chat ul li.chatbot { 
    text-align: left !important; 
    margin-right: 100px !important;
  }
  .chat_wrap .chat ul li.user { 
    text-align: left; 
    margin-left: 100px;
  }
  
  .chat_wrap .chat li:nth-child(even) {
    background-color: #ddd;
  }
  
  .chat_wrap .chat .sender {
    font-size: 14px;
    color: #aaa;
    margin-bottom: 4px;
  }
  
  .chat_wrap .input-div {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
  }
  
  .chat_wrap .input-div textarea {
    width: calc(100% - 70px);
    height: 50px;
    resize: none;
    font-size: 14px;
    padding: 10px;
    border-radius: 5px;
    border: none;
    background-color: #f2f2f2;
    margin-right: 10px;
  }
  
  .chat_wrap .input-div button {
    width: 60px;
    height: 50px;
    border-radius: 5px;
    border: none;
    background-color: #1c7731;
    color: #fff;
    font-size: 14px;
    font-weight: bold;
    cursor: pointer;
  }

  /* .chat_wrap .close-btn {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 16px;
    color: #aaa;
    cursor: pointer;
  } */

  .chat_wrap .close-btn {
    position: absolute;
    top: 5px;
    right: 10px;
    border: none;
    font-size: 1.5rem;
    color: #bebbbb;
    background-color: #1c7731;
    /* padding: 0.5rem; */
    cursor: pointer;
    transition: all 0.3s ease-in-out;
  }
  
  .chat_wrap .close-btn:hover {
    color: #ffffff;
  }

  /* chatbot button */
  .circular-button {
    position: fixed; /* 버튼이 페이지에 고정되도록 설정합니다. */
    bottom: 20px; /* 버튼이 페이지 하단에서 20px 떨어진 위치에 표시되도록 설정합니다. */
    left: 20px; /* 버튼이 페이지 좌측에서 20px 떨어진 위치에 표시되도록 설정합니다. */
    width: 100px; /* 버튼의 가로 길이를 50px로 설정합니다. */
    height: 100px; /* 버튼의 세로 길이를 50px로 설정합니다. */
    background-color: #ffffff; /* 버튼의 배경색을 초록색으로 설정합니다. */
    border-radius: 50%; /* 버튼을 원형으로 만들기 위해 border-radius 속성을 50%로 설정합니다. */
    box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.3); /* 버튼 주변에 약간의 그림자 효과를 주기 위해 box-shadow 속성을 사용합니다. */
    display: flex; /* 버튼 내부의 내용을 가운데 정렬하기 위해 display 속성을 flex로 설정합니다. */
    justify-content: center; /* 버튼 내부의 내용을 수평 가운데 정렬하기 위해 justify-content 속성을 center로 설정합니다 */
    align-items: center; /* 버튼 내부의 내용을 수직 가운데 정렬하기 위해 align-items 속성을 center로 설정합니다. */
    cursor: pointer; /* 마우스 커서를 버튼 위에 올렸을 때 손가락 모양으로 변경되도록 설정합니다. */
    z-index: 9999; /* 다른 요소와 겹치지 않도록 버튼을 최상위 레이어로 설정합니다. */
    font-weight: 800;
    color: #071f0d;
    font-family: Arial, Helvetica, sans-serif;

    animation: scale 0.5s ease-in-out; /* 클릭 애니메이션*/
  }
  
  .circular-button:hover {
    background-color: #75df2a; /* 마우스를 버튼 위로 올리면 배경색이 주황색에서 연한 주황색으로 변경됩니다. 이는 :hover 가상 클래스를 사용하여 구현됩니다. */
  }

  /* chatbot btn animation */
  @keyframes scale {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.2);
    }
    100% {
      transform: scale(1);
    }
  }
  